/**
 * TUTORIAL MODE
 * 
 * Automatically starts at the very beginning of the scene
 * Clock is deactivated, so users can spend as much time here as they want
 * 
 * To illustrate the way the scene works, we can put 1-2 objects to pick up
 * When users leave the tutorial mode, all stats will reset and clock will start
 * and they cannot revisit the tutorial area
 * 
 * 
 */

var tutorialMode = false

const groupTutorial = new THREE.Group()

var chair = new THREE.Object3D()
var crate = new THREE.Object3D()

loader.load(
    path+'chair'+fileType,
    (gltf) =>
    {   
        chair = gltf.scene
        chair.scale.set(10,12,10)
        chair.position.set(30,6,150)
        chair.name = 'chair'
        groupTutorial.add(chair)
    }
)

loader.load(
    path+'crate'+fileType,
    (gltf) =>
    {   
        crate = gltf.scene
        crate.scale.set(8,8,8)
        crate.position.set(30,7,120)
        crate.name = 'crate'
        groupTutorial.add(crate)
    }
)
scene.add(groupTutorial)

var activeGroup
var activeGroupNumItems

var activeItemIndex = 0

var target = new THREE.Vector3()
var activeItem

var activeItemZoom

var isStarting = false
const startingText = document.getElementById("startingText")

const tutorialDiv = document.getElementById("tutorialTexts")

//at the beginning, there was nothing
//then when users press Enter for the first time, they can follow the tutorials on screen (behaviour of keys/texts are controlled by keyboard key events)
//tutorial scene is loaded with some dummy objects that users can try out, but these won't affect the real scene for when they exit the tutorial mode
function startTutorial() {
    update() //update is only called when users press Enter for the first time. This will give the app time to set up the scene and parameters as below
    tutorialMode = true
    startingText.remove()
    tutorialDiv.children[0].style.visibility = "visible"
    tutorialDiv.children[1].style.visibility = "visible"
    activeGroup = groupTutorial
    activeGroupNumItems = activeGroup.children.length
    activeItem = activeGroup.children[activeItemIndex]
    activeItem.getWorldPosition(target)
    gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
}


const clock = new THREE.Clock()
clock.autoStart = false //clock is only activated once users exit Tutorial Mode


//when users press PageUp for the first time, they will leave tutorial mode
//and inventory will reset
//the objects from the tutorial scene will also disappear
function exitTutorial() {
    
    tutorialMode = false

    for (let c = 0; c < tutorialDiv.children.length; c++) {
        tutorialDiv.children[c].style.visibility = "hidden"
    }
    
    //remove any placards that were placed during tutorial and reset the placardArray to 0
    for (let p = 0; p < placardArray.length; p++) {
        scene.remove(placardArray[p])
    }
    placardArray = []

    for (let i = 0; i < inventoryList.length; i++) {
        inventorySlots.children[i].material.map = null
        inventorySlots.children[i].material.needsUpdate = true

        inventorySlots.children[i].material.color = 'black'
    }

    inventoryList = []
    scene.remove(groupTutorial)
    clock.start()
}


/**
 * Update
 */

const timer = document.getElementById('countdown')
const maxTime = 900 //15 minutes (= 900 seconds)

function update() {

    //We put this in update so that the panels will always follow camera's position when it moves from place to place
    inventoryGroup.position.x = camera.position.x
    inventoryGroup.position.z = camera.position.z-10

    overlay.position.set(camera.position.x,camera.position.y,camera.position.z)
    
    if (tutorialMode || closeUpMode || inventoryIsUp || analysingMode) {
        overlay.visible = true
    } else {
        overlay.visible = false
    }

    //update timer
    const elapsedTime = clock.getElapsedTime()

    var countdown = maxTime - elapsedTime
    var minutes = Math.floor((countdown / 60) % 60)
    var seconds = Math.floor(countdown % 60)
    const pad = (n) => n < 10 ? `0${n}` : n //so that numbers that are below 10 will appear with 0 in front e.g. 09 instead of 9

    timer.innerHTML = `${pad(minutes)}:${pad(seconds)}`

    //once the timer hits 0, set it up to do something
    if (countdown < 0) {
        timer.innerHTML = "time's up"
        overlay.visible = true
        location.reload() //this will refresh the webpage/reset the scene
    } 

    // Update controls -- only for dev, it should be disabled for 360imx build
    //controls.update()

    if (igloo.render() != true) {
        renderer.render( scene, camera )
    }
    // Update every frame
    window.requestAnimationFrame(update)
}


/**
 * Keyboard controller
 */

document.addEventListener( 'keydown', onKeyDown )

function onKeyDown( event ) {

if (!isStarting) {

    if (event.keyCode === 13) {
        startTutorial()
        isStarting = true
    }
} else {

if (!inventoryIsUp && !analysingMode && !closeUpMode) { // NORMAL MODE

    switch (event.keyCode)
    {
        case 33: //pageUp key : to switch location
            if (tutorialMode) {
                exitTutorial()
            }
            
            if (currectLocIndex === maxLocLength) {
                currectLocIndex = 0
            } else {
                currectLocIndex++ 
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })

            activeGroup = allGroups[currectLocIndex]
            activeGroupNumItems = activeGroup.children.length

            // boxLine (selection marker) will only show up if there are 1 or more items in the group
            if (activeGroupNumItems > 0) {
                scene.add(boxLine)
                activeItemIndex = 0 //reset active item index to 0 (first child of the group)

                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }
            
            break

        case 34: //pageDown key : to switch location

            if (currectLocIndex === 0) {
                currectLocIndex = maxLocLength
            } else {
                currectLocIndex--
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            
            activeGroup = allGroups[currectLocIndex]
            activeGroupNumItems = activeGroup.children.length

            if (activeGroupNumItems > 0) {
                scene.add(boxLine)
                activeItemIndex = 0 //reset active item index to 0 (first child of the group)

                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }

            break

        case 37: //left key : to select Object at active location

            if (tutorialMode) {
                tutorialDiv.children[0].style.visibility = "hidden"
                tutorialDiv.children[1].style.visibility = "hidden"
                tutorialDiv.children[2].style.visibility = "visible"
            }

            //check first if the activegroup still has items that can be selected

            if (activeGroupNumItems > 0 ) {
                scene.add(boxLine)
                if (activeItemIndex === 0) {
                    activeItemIndex = activeGroupNumItems-1
                } else {
                    activeItemIndex--
                }
                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }
            
			break
		
		case 39: //right key : to select Object at active location

            if (tutorialMode) {
                tutorialDiv.children[0].style.visibility = "hidden"
                tutorialDiv.children[1].style.visibility = "hidden"
                tutorialDiv.children[2].style.visibility = "visible"
            }

            //check first if the activegroup still has items that can be selected

            if (activeGroupNumItems > 0 ) {
                if (activeItemIndex === activeGroupNumItems-1) {
                    activeItemIndex = 0
                } else {
                    activeItemIndex++
                }
                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            } 
            
            break
        
        case 38: //up key : to open Inventory

            scene.add(inventoryModeText)
            inventoryModeText.position.set(camera.position.x-2.5,camera.position.y+2.2,camera.position.z-9)
            
                for (let i in inventoryList) {
                    inventorySlots.children[i].material = new THREE.MeshBasicMaterial(
                        {map: new THREE.TextureLoader().load(previewImgPath + inventoryList[i] + imgType)}
                    ) 
                }

                gsap.to(inventoryGroup.position, { y: camera.position.y+3.5, ease: "circle"})
                inventoryIsUp = true
            
                inventoryNum = inventoryList.length
            

            break

        case 40: //down key : to open CloseUp Mode
            
            if (activeGroupNumItems > 0) {

                scene.add(closeUpModeText)
                closeUpModeText.position.set(camera.position.x-3.5,camera.position.y+3.5,camera.position.z-9)
                
                activeItemZoom = activeItem.name

                loader.load(
                    path+activeItemZoom+fileType,
                    (gltf) =>
                    {   
                        zoomItem = gltf.scene
                        zoomItem.scale.set(2.5,2.5,2.5)
                        zoomItem.position.set(camera.position.x,camera.position.y-1,camera.position.z-10)
                        scene.add(zoomItem)
                    }
                )
                closeUpMode = true 
            }

            break

        case 13: //enter key : to store Object in inventory // once added to inventory, object cannot be placed back into scene

                //check first if the activegroup still has items that can be selected
                //if there are more than 1 items, removing selected item will automatically move the selection marker to the next item
                //if there is exactly 1 item in the group, removing that item will also remove the selection marker
                //if there are no more items to select, pressing Enter will prompt alert saying "no more items"

                if (activeGroupNumItems > 1 ) {

                    //limit the number of items user can store in inventory
                    //the number should match the total number of available slots
                    if (inventoryList.length <= maxSlot) { 

                        //save the name of the activeItem to the Inventory List before removing it from the scene
                        inventoryList.push(activeItem.name)

                        placard.position.copy(target) //first we save the location of the current item
                        let newPlacard = placard.clone()
                        placardArray.push(newPlacard)
                        scene.add(newPlacard) //then we clone the placard object so the original can still be used for others

                        activeGroup.remove(activeItem)
                        scene.remove(activeItem)

                        //if the item is the last one on the group list, the box will move to the first item instead (since there's no next one)
                        if (activeItemIndex === activeGroupNumItems-1) {
                            activeItemIndex = 0
                        }
 
                        activeItem = activeGroup.children[activeItemIndex]
                        activeItem.getWorldPosition(target)
                        gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})

                        activeGroupNumItems = activeGroup.children.length
                    } else {
                        console.log("exceeds inventory slots") // can change this to on-screen warning if necessary
                    }
                    
                } else if (activeGroupNumItems === 1) {
                    
                    //limit the number of items user can store in inventory
                    //the number should match the total number of available slots
                    if (inventoryList.length <= maxSlot) {

                        //save the name of the activeItem to the Inventory List before removing it from the scene
                        inventoryList.push(activeItem.name)

                        placard.position.copy(target) //first we save the location of the current item
                        let newPlacard = placard.clone()
                        placardArray.push(newPlacard)
                        scene.add(newPlacard) //then we clone the placard object so the original can still be used for others

                        activeGroup.remove(activeItem)
                        scene.remove(activeItem)

                        activeGroupNumItems = 0
                        scene.remove(boxLine)
                    } else {
                        console.log("exceeds inventory slots") // can change this to on-screen warning if necessary
                    }

                } else {
                    console.log("no more items!") // to be replaced by on-screen warning
                }


			break
    }
} else if (inventoryIsUp && !analysingMode && !closeUpMode) { //INVENTORY MODE

    switch (event.keyCode) {

        case 38: //up key : to return to Normal mode

            if (tutorialMode) {
                tutorialDiv.children[3].style.visibility = "hidden"
                tutorialDiv.children[4].style.visibility = "visible"
            }

            scene.remove(inventoryModeText)
            inventoryModeText.dispose()

            gsap.to(inventoryGroup.position, { y: camera.position.y+5.5, ease: "circle"})
            inventoryIsUp = false
        break

        case 39: //right key : to select object in inventory

        if (inventoryNum > 0) {
            if (invSelectionIndex === inventoryNum - 1) {
                invSelectionIndex = 0
            } else {
                invSelectionIndex++
            } 
            inventoryMarker.position.x = inventorySlots.children[invSelectionIndex].position.x
        }
        break

        case 37: //left key : to select object in inventory

        if (inventoryNum > 0) {
            if (invSelectionIndex === 0) {
                invSelectionIndex = inventoryNum - 1
            } else {
                invSelectionIndex--
            } 
            inventoryMarker.position.x = inventorySlots.children[invSelectionIndex].position.x
        }
        break

        case 13: //enter key : To open analysing mode

        if (inventoryNum > 0) {

            scene.add(analysingModeText, pgDownPrompt, pgUpPrompt)
            analysingModeText.position.set(camera.position.x-3.5,camera.position.y+2.2,camera.position.z-9)

            pgUpPrompt.position.set(camera.position.x-2.5,camera.position.y+1,camera.position.z-9)
            pgDownPrompt.position.set(camera.position.x+2.5,camera.position.y+1,camera.position.z-9)

            scene.remove(inventoryModeText)
            inventoryModeText.dispose()
            
            let invSelectedItem = inventoryList[invSelectionIndex]

            loader.load(
                path+invSelectedItem+fileType,
                (gltf) =>
                {   
                    analyseItem = gltf.scene
                    analyseItem.scale.set(2,2,2)
                    analyseItem.position.set(camera.position.x,camera.position.y-2,camera.position.z-10)
                    scene.add(analyseItem)
                }
            )
            analysingMode = true
          
        }
        break

    }
} else if (analysingMode && !closeUpMode) { //ANALYSING MODE

    let invSelectedItem = inventoryList[invSelectionIndex]

    switch (event.keyCode) {
        case 13: //enter key : to return to inventory mode
        scene.remove(analyseItem, analysingModeText, pgDownPrompt, pgUpPrompt, swabText, brushingText)
        analysingModeText.dispose()
        swabText.dispose()
        brushingText.dispose()

        scene.add(inventoryModeText)
        inventoryModeText.position.set(camera.position.x-2.5,camera.position.y+2.2,camera.position.z-9)

        analysingMode = false
        break

        //arrow keys are used to rotate objects
        case 39: //right key
        analyseItem.rotateY(0.1)
        break

        case 37: //left key
        analyseItem.rotateY(-0.1)
        break

        case 38: //up key
        analyseItem.rotateX(-0.1)
        break

        case 40: //down key
        analyseItem.rotateX(0.1)
        break

        case 33: //pageUp key : brush item to find fingerprints

        brushingText.position.set(camera.position.x-3.5,camera.position.y,camera.position.z-9)

        const newPath = 'models/brushed/'

        switch (invSelectedItem) {
            case "chair":
            case "axe":
            case "hammer":
            case "knifeblock":
            case "tea":
                brushingText.text = "found prints after brushing "+invSelectedItem
                scene.remove(analyseItem)
        
                loader.load(
                    newPath+invSelectedItem+fileType,
                    (gltf) =>
                    {   
                        analyseItem = gltf.scene
                        analyseItem.scale.set(2,2,2)
                        analyseItem.position.set(camera.position.x,camera.position.y-2,camera.position.z-10)
                        scene.add(analyseItem)
                    }
                )
                break

            default:
                brushingText.text = "prints not found"   
        }
        brushingText.sync()
        scene.add(brushingText)

        break

        case 34: //pageDown key : swab item to analyse substance

        swabText.position.set(camera.position.x+1.5,camera.position.y,camera.position.z-9)

        switch (invSelectedItem) {
            case "banana": swabText.text = "found substance: banana" ; break
            case "cake": swabText.text = "found substance: cake icing"; break
            case "crate": swabText.text = "found substance: black paint"; break
            case "dog": swabText.text = "found substance: cake icing"; break
            case "axe": swabText.text = "found substance: blood"; break
            case "hammer": swabText.text = "found substance: red paint"; break
            case "tea": swabText.text = "found substance: tea"; break
            case "wine": swabText.text = "found substance: wine"; break

            default: swabText.text = "no noteworthy substance found"
        }
        swabText.sync()
        scene.add(swabText)

        break
    }
} else if (closeUpMode && !analysingMode) { //CLOSEUP MODE

    const multiplier = 1.2

    switch (event.keyCode) {
        case 13: //enter key : to return to normal mode

        if (tutorialMode) {
            tutorialDiv.children[2].style.visibility = "hidden"
            tutorialDiv.children[3].style.visibility = "visible"
        }

        scene.remove(zoomItem,closeUpModeText)
        closeUpModeText.dispose()
        closeUpMode = false
        break

        //arrow keys are used to rotate object
        case 39: //right key
        zoomItem.rotateY(0.1)
        break

        case 37: //right key
        zoomItem.rotateY(-0.1)
        break

        case 38: //up key
        zoomItem.rotateX(-0.1)
        break

        case 40: //down key
        zoomItem.rotateX(0.1)
        break

        case 33: //pageUp key : to enlarge object

        const maxScale = 6

        if (zoomItem.scale.x <= maxScale && zoomItem.scale.y <= maxScale && zoomItem.scale.z <= maxScale) {
            zoomItem.scale.x = zoomItem.scale.x * multiplier
            zoomItem.scale.y = zoomItem.scale.y * multiplier
            zoomItem.scale.z = zoomItem.scale.z * multiplier
        }
        
        break

        case 34: //pageDown key : to reduce object size

        const minScale = 0.8

        if (zoomItem.scale.x >= minScale && zoomItem.scale.y >= minScale && zoomItem.scale.z >= minScale) {
            zoomItem.scale.x = zoomItem.scale.x / multiplier
            zoomItem.scale.y = zoomItem.scale.y / multiplier
            zoomItem.scale.z = zoomItem.scale.z / multiplier
        }

        break
    }
}  
} 
}

